import Layer from './layer';

class WMSLayer extends Layer {
  constructor(config) {
    super(config);

    this.layer = new ol.layer.Image({
      title: this.config.title,
      visible: this.config.visible,
      exclusive: this.config.exclusive,
      zIndex: 1,
    });

    this.source = new ol.source.ImageWMS({
      url: `${this.config.serverURL}/wms/`,
      params: {
        LAYERS: this.config.layer,
        STYLES: this.config.style,
      },
      attributions: [new ol.Attribution({
        html: this.config.attribution,
      })],
    });

    this.layer.setSource(this.source);
  }

  filter(filters = []) {
    super.filter(filters);
    if (this.filterString !== '') {
      const params = this.layer.getSource().getParams();
      params.CQL_FILTER = this.filterString;
      this.source.updateParams(params);
    }
  }
}

module.exports = WMSLayer;



