import '../styles/widget-manager.scss';

class WidgetManager {
  constructor(dashboard) {
    this.dashboard = dashboard;
    this.serverURL = `${dashboard.serverURL}/wps/`;
    this.widgets = [];
  }

  render() {
    this.widgets.forEach((widget) => {
      widget.render();
    });
  }

  refresh() {
    this.extent = this.dashboard.extent;
    this.widgets.forEach((widget) => {
      widget.refresh();
    });
  }

  addWidget(widget) {
    widget.manager = this;
    this.widgets.push(widget);
  }
}

export default WidgetManager;



