import parser from 'xml2js';

import Widget from './widget';

import template from '../templates/wps-widget.hbs';

class WPSWidget extends Widget {
  constructor(config) {
    super(config);
    this.layer = config.layer;
    this.template = template;
  }

  format() {
    return parseInt(this.value, 10);
  }

  refresh() {
    console.log(this.manager.extent);
    fetch(this.manager.serverURL, {
      method: 'POST',
      body: this.requestTemplate({
        layer: this.layer.layerName,
        property: this.property,
        function: this.function,
        group: this.group,
        extent: this.manager.extent,
        filters: this.layer.filters,
      }),
      headers: new Headers({
        'Content-Type': 'text/xml',
      }),
    }).then(response => response.text()).then((data) => {
      console.log(data);
      parser.parseString((error, result) => console.log(result));
      // self.updateValue(response);
      // if(self.container) {
      //   self.container.children('.widget-content').html(Handlebars.templates['wps-widget.hbs']({
      //     value: self.value
      //   }));
      // }
      this.value = Math.random() * 10000;
      super.refresh();
    });
  }
}

module.exports = WPSWidget;



