import MapManager from './map-manager';
import WidgetManager from './widget-manager';

import template from '../templates/dashboard.hbs';

import '../styles/dashboard.scss';

class Dashboard {
  constructor(config) {
    this.config = config;
    this.container = config.container;
    this.serverURL = config.serverURL;

    this.mapManager = new MapManager(Object.assign({
      serverURL: this.config.serverURL,
      attribution: this.config.attribution,
    }, this.config.map));

    this.widgetManager = new WidgetManager(this);

    this.mapManager.on('mapchange', (event) => {
      this.extent = event.extent;
      this.refresh();
    });
  }

  render() {
    this.container.insertAdjacentHTML('beforeend', template({
      config: this.config,
    }));

    this.mapManager.render(this.container.getElementsByClassName('map')[0]);

    this.widgetManager.container = this.container.getElementsByClassName('widget-panel')[0];
    this.widgetManager.render();
  }

  refresh() {
    this.widgetManager.refresh();
  }

  addWFSLayer(config) {
    return this.mapManager.addWFSLayer(config);
  }

  addWMSLayer(config) {
    return this.mapManager.addWMSLayer(config);
  }

  addWidget(widget) {
    this.widgetManager.addWidget(widget);
  }
}

export default Dashboard;



