class Layer {
  constructor(config) {
    this.config = config;

    this.filters = this.config.filters || [];
    this.filters.forEach((filter) => {
      filter.ogcOperator = Layer.getOgcOperator(filter.operator);
    });

    this.layerName = this.config.layer;
  }

  filter(filters = []) {
    filters.push(...this.filters);
    this.filterString = filters.map(filter =>
      `${filter.property}${(filter.operator) ? filter.operator : '='}'${filter.value}'`
    ).join(' AND ');
  }

  static getOgcOperator(operator) {
    switch (operator) {
      case '<>':
        return 'PropertyIsNotEqualTo';
      case '<':
        return 'PropertyIsLessThan';
      case '<=':
        return 'PropertyIsLessThanOrEqualTo';
      case '>':
        return 'PropertyIsGreaterThan';
      case '>=':
        return 'PropertyIsGreaterThanOrEqualTo';
      case 'like':
        return 'PropertyIsLike';
      default:
        return 'PropertyIsEqualTo';
    }
  }
}

module.exports = Layer;



